<template>
  <div class='user-profile-page'>
    <van-nav-bar left-arrow @click-left="$router.back()" title="个人信息"></van-nav-bar>
    <van-cell-group>
      <van-cell is-link title="头像" center @click="showPhoto=true">
        <van-image
          slot="default"
          fit="cover"
          round
          :src="userInfo.photo"
        />
      </van-cell>
      <van-cell is-link title="昵称" :value="userInfo.name" @click="showName=true;name=userInfo.name" />
    </van-cell-group>
    <van-cell-group style="margin-top:12px">
      <van-cell is-link title="性别" :value="userInfo.gender===0?'男':'女'" @click="showGender=true"/>
      <van-cell is-link title="生日" :value="userInfo.birthday || '未填写'" @click="showBirthday=true;birthday=new Date(userInfo.birthday)" />
    </van-cell-group>
    <div class="logout">
      <span @click="logout">退出登录</span>
    </div>
    <!-- 修改昵称 -->
    <van-popup class="my-popup" v-model="showName" position="right">
      <van-nav-bar
        left-arrow
        @click-left="showName=false"
        right-text="保存"
        @click-right="modifyNickName"
        title="修改昵称"
      />
      <van-field v-model="name"></van-field>
    </van-popup>
    <!-- 修改性别 -->
    <van-action-sheet v-model="showGender" :actions="[{ name: '男' }, { name: '女' }]" cancel-text="取消"
  close-on-click-action
   @select="onSelect" />

   <!-- 选择生日日期 -->
       <!-- 修改生日 -->
    <van-popup v-model="showBirthday" position="bottom">
      <van-datetime-picker
        v-model="birthday"
        type="date"
        title="选择年月日"
        :min-date="minDate"
        :max-date="maxDate"
        @cancel="showBirthday=false"
        @confirm="saveBirthday"
      />
    </van-popup>

    <!-- 修改头像-弹出层 -->
    <input type="file" style="display:none" ref="file" @change="updateImage">
    <van-action-sheet
      v-model="showPhoto"
      :actions="[{ name: '拍照', value: 0 },{ name: '本地选择', value: 1 }]"
      @select="onPhotoSelect"
      cancel-text="取消"
    />
  </div>
</template>
<script>
import {
  getUserInfo,
  editUserInfo,
  editUserPhoto
} from '@/api/user'
import { photoCompress } from '@/utils/compress.js'
import dayjs from 'dayjs'
export default {
  name: 'UserProfilePage',
  data () {
    return {
      userInfo: {},
      showName: false,
      name: '',
      showGender: false,
      showBirthday: false,
      birthday: new Date(),
      minDate: new Date('1990/1/1'),
      maxDate: new Date('2030/1/1'),
      showPhoto: false
    }
  },
  created () {
    this.getUserInfo()
  },
  methods: {
    updateImage () {
      // 需要对this.$refs.file.files[0]文件进行压缩
      photoCompress(this.$refs.file.files[0], { quality: 0.1 }, async (res) => {
        var fd = new FormData()
        fd.append('photo', res)
        const [err, data] = await editUserPhoto(fd)
        if (err) return this.$toast.fail(err)
        console.log(data)
        // 隐藏actionsheet
        this.showPhoto = false
        this.getUserInfo()
      })
    },
    // 拍照或者本地上传
    onPhotoSelect (obj) {
      console.log(obj.name)
      if (obj.name === '拍照') {
        // 手机拍照上传
      } else {
        // 本地上传,弹出文件夹选择图片
        this.$refs.file.click()
      }
    },
    // 修改生日
    async saveBirthday () {
      const tempUserUser = Object.assign({}, this.userInfo)
      // 后台要的不是生日数据，而是字符串
      tempUserUser.birthday = dayjs(this.birthday).format('YYYY-MM-DD')
      await this.saveUserInfo(tempUserUser)

      // 隐藏弹出层
      this.showBirthday = false
    },
    // x选中某一项的时候
    async onSelect (obj) {
    //   console.log(obj.name)
      const tempUserUser = Object.assign({}, this.userInfo)
      if (obj.name === '男') {
        tempUserUser.gender = 0
      } else {
        tempUserUser.gender = 1
      }
      await this.saveUserInfo(tempUserUser)
    },
    // 获取用户个人信息
    async getUserInfo () {
      const [err, data] = await getUserInfo()
      if (err) return this.$toast.fail(err)
      //   console.log(data)
      this.userInfo = data.data.data
    },
    // 修改用户昵称
    async modifyNickName () {
      const tempUserUser = Object.assign({}, this.userInfo)
      tempUserUser.name = this.name
      await this.saveUserInfo(tempUserUser)
      // 1.清空name
      this.text = ''
      // 2.退出当前弹出层
      this.showName = false
    },
    // 修改用户昵称
    async saveUserInfo (userInfo) {
      const [err, data] = await editUserInfo(userInfo)
      if (err) this.$toast.fail(err)
      console.log(data)
      // 3.再次请求个人资料
      this.getUserInfo()
    },
    logout () {
      this.$dialog.confirm({
        title: '温馨提示',
        message: '您确认退出极客园吗？',
        theme: 'round-button'
      }).then(() => {
        console.log('点击确认了')
        // 1.清空token
        this.$store.commit('user/setToken', '')
        // 2.进入登录页面
        this.$router.replace('/')
        this.$toast.success('退出登录')
      }, () => {
        console.log('点击取消了')
      })
    }
  }
}
</script>
<style lang="less" scoped>
.user-profile-page {
  background: #f8f8f8;
  .van-image {
    display: block;
    float: right;
    width: 30px;
    height: 30px;
  }
  .van-cell__title {
    width: 50px;
    flex: none;
  }
}
.logout {
  text-align: center;
  position: absolute;
  left: 0;
  bottom: 30px;
  width: 100%;
  color: @geek-color;
}
.my-popup {
  width: 100%;
  height: 100%;
  /deep/ .van-nav-bar__text {
    color: @geek-color;
  }
  /deep/ .van-field__control {
    background: @geek-gray-color;
    padding: 10px;
    border-radius: 4px;
  }
}
</style>
